import React, { useState } from 'react';
import { Plus, Edit, Trash2, Search, Calendar, Clock, CheckCircle, XCircle, Settings } from 'lucide-react';

interface Semester {
  id: string;
  name: string;
  code: string;
  academicYear: string;
  type: 'Ganjil' | 'Genap' | 'Pendek';
  startDate: string;
  endDate: string;
  registrationStart: string;
  registrationEnd: string;
  status: 'Active' | 'Upcoming' | 'Completed' | 'Draft';
  totalStudents: number;
  totalClasses: number;
}

interface AcademicCalendar {
  id: string;
  semesterId: string;
  title: string;
  startDate: string;
  endDate?: string;
  type: 'Registration' | 'Lecture' | 'Exam' | 'Holiday' | 'Assignment';
  description: string;
}

const SemesterSetup: React.FC = () => {
  const [activeTab, setActiveTab] = useState<'semesters' | 'calendar'>('semesters');
  const [searchTerm, setSearchTerm] = useState('');

  // Mock data
  const semesters: Semester[] = [
    {
      id: '1',
      name: 'Semester Ganjil 2024/2025',
      code: '20241',
      academicYear: '2024/2025',
      type: 'Ganjil',
      startDate: '2024-09-01',
      endDate: '2024-12-30',
      registrationStart: '2024-08-15',
      registrationEnd: '2024-08-31',
      status: 'Active',
      totalStudents: 1250,
      totalClasses: 125
    },
    {
      id: '2',
      name: 'Semester Genap 2024/2025',
      code: '20242',
      academicYear: '2024/2025',
      type: 'Genap',
      startDate: '2025-02-01',
      endDate: '2025-05-31',
      registrationStart: '2025-01-15',
      registrationEnd: '2025-01-31',
      status: 'Upcoming',
      totalStudents: 0,
      totalClasses: 0
    },
    {
      id: '3',
      name: 'Semester Pendek 2024/2025',
      code: '20243',
      academicYear: '2024/2025',
      type: 'Pendek',
      startDate: '2025-06-01',
      endDate: '2025-07-31',
      registrationStart: '2025-05-15',
      registrationEnd: '2025-05-31',
      status: 'Draft',
      totalStudents: 0,
      totalClasses: 0
    },
    {
      id: '4',
      name: 'Semester Genap 2023/2024',
      code: '20232',
      academicYear: '2023/2024',
      type: 'Genap',
      startDate: '2024-02-01',
      endDate: '2024-05-31',
      registrationStart: '2024-01-15',
      registrationEnd: '2024-01-31',
      status: 'Completed',
      totalStudents: 1180,
      totalClasses: 118
    }
  ];

  const academicCalendar: AcademicCalendar[] = [
    {
      id: '1',
      semesterId: '1',
      title: 'Registrasi Mata Kuliah',
      startDate: '2024-08-15',
      endDate: '2024-08-31',
      type: 'Registration',
      description: 'Periode registrasi mata kuliah untuk mahasiswa'
    },
    {
      id: '2',
      semesterId: '1',
      title: 'Perkuliahan Dimulai',
      startDate: '2024-09-01',
      type: 'Lecture',
      description: 'Hari pertama perkuliahan semester ganjil'
    },
    {
      id: '3',
      semesterId: '1',
      title: 'Ujian Tengah Semester',
      startDate: '2024-10-15',
      endDate: '2024-10-30',
      type: 'Exam',
      description: 'Periode ujian tengah semester'
    },
    {
      id: '4',
      semesterId: '1',
      title: 'Libur Semester',
      startDate: '2024-12-20',
      endDate: '2024-12-30',
      type: 'Holiday',
      description: 'Libur akhir semester'
    }
  ];

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'Active':
        return 'bg-green-100 text-green-800';
      case 'Upcoming':
        return 'bg-blue-100 text-blue-800';
      case 'Completed':
        return 'bg-gray-100 text-gray-800';
      case 'Draft':
        return 'bg-yellow-100 text-yellow-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const getTypeColor = (type: string) => {
    switch (type) {
      case 'Ganjil':
        return 'bg-blue-100 text-blue-800';
      case 'Genap':
        return 'bg-green-100 text-green-800';
      case 'Pendek':
        return 'bg-purple-100 text-purple-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const getCalendarTypeColor = (type: string) => {
    switch (type) {
      case 'Registration':
        return 'bg-blue-100 text-blue-800';
      case 'Lecture':
        return 'bg-green-100 text-green-800';
      case 'Exam':
        return 'bg-red-100 text-red-800';
      case 'Holiday':
        return 'bg-yellow-100 text-yellow-800';
      case 'Assignment':
        return 'bg-purple-100 text-purple-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('id-ID', {
      day: 'numeric',
      month: 'long',
      year: 'numeric'
    });
  };

  const filteredSemesters = semesters.filter(semester =>
    semester.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    semester.code.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const renderSemesters = () => (
    <div className="space-y-6">
      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="card p-6">
          <div className="flex items-center">
            <div className="p-2 bg-green-100 rounded-lg">
              <CheckCircle className="h-6 w-6 text-green-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm text-gray-500">Semester Aktif</p>
              <p className="text-2xl font-semibold text-gray-900">
                {semesters.filter(s => s.status === 'Active').length}
              </p>
            </div>
          </div>
        </div>
        <div className="card p-6">
          <div className="flex items-center">
            <div className="p-2 bg-blue-100 rounded-lg">
              <Clock className="h-6 w-6 text-blue-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm text-gray-500">Akan Datang</p>
              <p className="text-2xl font-semibold text-gray-900">
                {semesters.filter(s => s.status === 'Upcoming').length}
              </p>
            </div>
          </div>
        </div>
        <div className="card p-6">
          <div className="flex items-center">
            <div className="p-2 bg-yellow-100 rounded-lg">
              <Settings className="h-6 w-6 text-yellow-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm text-gray-500">Draft</p>
              <p className="text-2xl font-semibold text-gray-900">
                {semesters.filter(s => s.status === 'Draft').length}
              </p>
            </div>
          </div>
        </div>
        <div className="card p-6">
          <div className="flex items-center">
            <div className="p-2 bg-gray-100 rounded-lg">
              <XCircle className="h-6 w-6 text-gray-600" />
            </div>
            <div className="ml-4">
              <p className="text-sm text-gray-500">Selesai</p>
              <p className="text-2xl font-semibold text-gray-900">
                {semesters.filter(s => s.status === 'Completed').length}
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Semesters Grid */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {filteredSemesters.map((semester) => (
          <div key={semester.id} className="card p-6">
            <div className="flex items-start justify-between mb-4">
              <div>
                <h3 className="text-lg font-semibold text-gray-900">{semester.name}</h3>
                <p className="text-sm text-gray-500">Kode: {semester.code}</p>
              </div>
              <div className="flex space-x-2">
                <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getTypeColor(semester.type)}`}>
                  {semester.type}
                </span>
                <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getStatusColor(semester.status)}`}>
                  {semester.status}
                </span>
              </div>
            </div>

            <div className="space-y-3">
              <div className="grid grid-cols-2 gap-4 text-sm">
                <div>
                  <span className="text-gray-500">Mulai:</span>
                  <div className="font-medium">{formatDate(semester.startDate)}</div>
                </div>
                <div>
                  <span className="text-gray-500">Selesai:</span>
                  <div className="font-medium">{formatDate(semester.endDate)}</div>
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4 text-sm">
                <div>
                  <span className="text-gray-500">Registrasi:</span>
                  <div className="font-medium">
                    {formatDate(semester.registrationStart)} - {formatDate(semester.registrationEnd)}
                  </div>
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4 text-sm">
                <div className="flex items-center space-x-2">
                  <span className="text-gray-500">Mahasiswa:</span>
                  <span className="font-medium text-blue-600">{semester.totalStudents.toLocaleString()}</span>
                </div>
                <div className="flex items-center space-x-2">
                  <span className="text-gray-500">Kelas:</span>
                  <span className="font-medium text-green-600">{semester.totalClasses}</span>
                </div>
              </div>
            </div>

            <div className="flex space-x-2 mt-4 pt-4 border-t border-gray-200">
              <button className="flex-1 btn-secondary flex items-center justify-center space-x-2">
                <Edit className="h-4 w-4" />
                <span>Edit</span>
              </button>
              <button className="flex-1 btn-primary">
                Kelola
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );

  const renderCalendar = () => (
    <div className="space-y-6">
      <div className="card">
        <div className="p-6 border-b border-gray-200">
          <h3 className="text-lg font-semibold text-gray-900">Kalender Akademik</h3>
          <p className="text-sm text-gray-500">Jadwal kegiatan akademik semester aktif</p>
        </div>
        <div className="divide-y divide-gray-200">
          {academicCalendar.map((event) => (
            <div key={event.id} className="p-6 hover:bg-gray-50">
              <div className="flex items-start justify-between">
                <div className="flex-1">
                  <div className="flex items-center space-x-3">
                    <h4 className="text-sm font-medium text-gray-900">{event.title}</h4>
                    <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getCalendarTypeColor(event.type)}`}>
                      {event.type}
                    </span>
                  </div>
                  <p className="text-sm text-gray-500 mt-1">{event.description}</p>
                  <div className="flex items-center space-x-4 mt-2 text-sm text-gray-500">
                    <div className="flex items-center space-x-1">
                      <Calendar className="h-4 w-4" />
                      <span>
                        {formatDate(event.startDate)}
                        {event.endDate && ` - ${formatDate(event.endDate)}`}
                      </span>
                    </div>
                  </div>
                </div>
                <div className="flex space-x-2 ml-4">
                  <button className="text-blue-600 hover:text-blue-900">
                    <Edit className="h-4 w-4" />
                  </button>
                  <button className="text-red-600 hover:text-red-900">
                    <Trash2 className="h-4 w-4" />
                  </button>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-3">
          <Calendar className="h-8 w-8 text-blue-600" />
          <div>
            <h1 className="text-2xl font-bold text-gray-900">Setup Semester</h1>
            <p className="text-gray-600">Kelola semester dan kalender akademik</p>
          </div>
        </div>
        <button className="btn-primary flex items-center space-x-2">
          <Plus className="h-4 w-4" />
          <span>Tambah Semester</span>
        </button>
      </div>

      {/* Tabs */}
      <div className="border-b border-gray-200">
        <nav className="-mb-px flex space-x-8">
          <button
            onClick={() => setActiveTab('semesters')}
            className={`py-2 px-1 border-b-2 font-medium text-sm ${
              activeTab === 'semesters'
                ? 'border-blue-500 text-blue-600'
                : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
            }`}
          >
            Semester
          </button>
          <button
            onClick={() => setActiveTab('calendar')}
            className={`py-2 px-1 border-b-2 font-medium text-sm ${
              activeTab === 'calendar'
                ? 'border-blue-500 text-blue-600'
                : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
            }`}
          >
            Kalender Akademik
          </button>
        </nav>
      </div>

      {/* Search */}
      {activeTab === 'semesters' && (
        <div className="relative w-full sm:w-80">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
          <input
            type="text"
            placeholder="Cari semester..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="input-field pl-10 w-full"
          />
        </div>
      )}

      {/* Content */}
      {activeTab === 'semesters' && renderSemesters()}
      {activeTab === 'calendar' && renderCalendar()}
    </div>
  );
};

export default SemesterSetup;